<template>
	<div class="player">
		<div class="box">
			<div class="bj">
				<div class="zhezhao"></div>
				<img :src="a" alt="">
			</div>
			<!-- 背景图 -->
			<!-- 	<div class="beijingtu">
				<img src="" alt="">
			</div> -->
			<div class="baitiao">
				<div class="content">
					<img id="bai" src="../../public/wyyimg/datie.png" alt="">
				</div>

			</div>

			<!-- 中间旋转图片 -->
			<div class="middle">
				<div class="tp">
					<img src="../../public/wyyimg/datie1.png" alt="">
				</div>
				<div class="songpictures">
					<div class="xuanzhuan" :class="{qc:is}">
						<img :src="a" alt="">
					</div>
				</div>
			</div>
			
			<!-- 底部 -->
			<!-- 音乐播放器 -->
			<audio id="audio" :src="this.b" ref="au" @timeupdate="stime" controls autoplay="autoplay">你的浏览器不支持audio</audio>

			<div class="base">

				<div class="s">
					<span class="k">00:00</span>
					<div ref="controllnfo" class="progress">
						<!-- 激活进度条 -->
						<div id="activeProgress" ref="activeProgress" class="active-progress"></div>
						     
						<!-- 滑块 -->
						     <div id="progressMask" ref="mask" class="mask"></div>
						      
						<!-- 绑定事件层 -->
						     <div id="progressLayer" class="layer" @touchstart="moveStart" @touchmove="move"></div>
					</div>
					<span class="j">04:00</span>
				</div>


				<div class="">
					<div class="shangyishou">
						<img src="../../public/wyyimg/shangyishou.png" alt="" @click="ggs()">
					</div>
					<div class="pofang">
						<img :src="sr" alt="" @click="cancel()">
					</div>
					<div class="xiayishou">
						<img src="../../public/wyyimg/xiayishou.png" alt="" @click="gg()">
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'

	export default {
		name: 'player',
		data() {
			return {
				a: '',
				b: '',
				is: true,
				sr: '/wyyimg/bofang.png',
				index: 0,
				data: []
			};
		},
		created() {
			//接收参数
			this.index = this.$route.params.index
			this.data = this.$route.params.son
			// console.log('qq',this.data)


			//接收音乐图片
			this.a = this.$route.params.a
			//接收音乐id
			this.b = this.$route.params.b
			// 			console.log(this.a)
			// 			console.log('ss',this.b)

			//获取音乐url
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/song/url?id=' + this.b)
				.then(res => {
					this.b = res.data.data[0].url
					// 				console.log(this.b)
					console.log(res.data.data[0].url)
				})
				.catch(err => {
					console.log("请求失败" + err)
				})
		},
		methods: {
			moveStart(e) {
				var a = document.querySelector('#audio');
				var pageX = e.targetTouches[0].pageX;
				var offsetLeft = this.$refs.controllnfo.offsetLeft;
				//判断起始点
				var controllnfoWidth = parseFloat(getComputedStyle(this.$refs.controllnfo).width)
				//获取大的width
				// 滑块大小
				var maskWidth = parseFloat(getComputedStyle(this.$refs.mask).width);
				//滑块移动的最大距离
				var maskMaxMoveLeft = controllnfoWidth - maskWidth;
				//滑块移动的最最小距离
				var maskMinMoveLeft = 0;
				//鼠标位置
				var distance = pageX - offsetLeft;
				if (distance > controllnfoWidth) {
					return;
				}
				// 激活进度条
				this.$refs.activeProgress.style.width = distance + 'px';
				//设置滑块移动
				distance = distance <= maskMinMoveLeft ? maskMinMoveLeft : distance >= maskMaxMoveLeft ? maskMaxMoveLeft : distance;
				this.$refs.mask.style.left = distance + 'px';

				var percent = (pageX - offsetLeft) / controllnfoWidth;
				console.log(percent)
				a.currentTime = a.duration * percent;
				console.log(a.duration * percent)
			},
			move(e) {
				this.moveStart(e);
			},
			stime() {
				var m = document.querySelector('.mask');
				var ac = document.querySelector('.active-progress');
				var percent = this.$refs.au.currentTime / this.$refs.au.duration;
				var controllnfoWidth = parseFloat(getComputedStyle(this.$refs.controllnfo).width);
				var maskWidth = parseFloat(getComputedStyle(this.$refs.mask).width);
				m.style.left = controllnfoWidth * percent + 'px';
				ac.style.width = percent * controllnfoWidth + 'px';
			},

			//上一首
			ggs() {
				if (this.index > 0) {
					this.index = this.index - 1
					this.b = this.data[this.index].id
					//请求歌曲id
					axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/song/url?id=' + this.b)
						.then(res => {
							this.b = res.data.data[0].url
							// 				console.log(this.b)
							console.log(res.data.data[0].url)
						})
						.catch(err => {
							console.log("请求失败" + err)
						})
					// console.log('a',this.data[this.index].al.picUrl)
					this.a = this.data[this.index].al == undefined ? this.data[this.index].song.album.picUrl : this.data[this.index].al
						.picUrl
				} else {
					this.index = this.data.length - 1
					this.b = this.data[this.index].id
					// 请求歌曲id
					axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/song/url?id=' + this.b)
						.then(res => {
							this.b = res.data.data[0].url
							// 				console.log(this.b)
							console.log('sss', res.data.data[0].url)
						})
						.catch(err => {
							console.log("请求失败" + err)
						})
					this.a = this.data[this.index].al == undefined ? this.data[this.index].song.album.picUrl : this.data[this.index].al
						.picUrl
					// 					this.is = !this.is
					// 					var a= document.querySelector('#audio')
					// 					var bs = document.querySelector('.content')
					// 					bs.style.transform = 'rotateZ(-25deg)'
					// 					this.sr = '/wyyimg/bofang.png'
				}
			},
			//下一首
			gg() {
				if (this.index < this.data.length - 1) {
					this.index = this.index + 1
					this.b = this.data[this.index].id
					//请求歌曲id
					axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/song/url?id=' + this.b)
						.then(res => {
							this.b = res.data.data[0].url
							// 				console.log(this.b)
							console.log(res.data.data[0].url)
						})
						.catch(err => {
							console.log("请求失败" + err)
						})
					this.a = this.data[this.index].al == undefined ? this.data[this.index].song.album.picUrl : this.data[this.index].al
						.picUrl
				} else {
					this.index = 0
					this.b = this.data[this.index].id
					//请求歌曲id
					axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/song/url?id=' + this.b)
						.then(res => {
							this.b = res.data.data[0].url
							// 				console.log(this.b)
							console.log(res.data.data[0].url)
						})
						.catch(err => {
							console.log("请求失败" + err)
						})
					this.a = this.data[this.index].al == undefined ? this.data[this.index].song.album.picUrl : this.data[this.index].al
						.picUrl
					// 						var a= document.querySelector('#audio')
					// 						var bs = document.querySelector('.content')
					// 						bs.style.transform = 'rotateZ(-25deg)'
					// 						this.sr = '/wyyimg/bofang.png'
					// 						
					// 						a.pause();				
				}
			},

			//判断停止播放图标  暂停播放开始播放
			cancel() {
				if (this.is) {
					this.is = !this.is
					var a = document.querySelector('#audio');
					var bs = document.querySelector('.content')
					bs.style.transform = 'rotateZ(0deg)'
					this.sr = '/wyyimg/zanting.png'
					a.play();
				} else {
					this.is = !this.is
					var a = document.querySelector('#audio')
					var bs = document.querySelector('.content')
					bs.style.transform = 'rotateZ(-25deg)'
					this.sr = '/wyyimg/bofang.png'
					a.pause();
				}
			}
		},
		mounted() {
			var a= document.querySelector('#audio');
			var k = document.querySelector('.k');
			a.ontimeupdate = function(){
			var currentTime = parseInt(this.currentTime);
			currentTime = currentTime<10?'0'+currentTime : currentTime>60?currentTime%60<10?parseInt(currentTime/60)+':0'+parseInt(currentTime%60):parseInt(currentTime/60)+':'+parseInt(currentTime%60):currentTime;
			k.innerHTML = currentTime
			} 
		}
	}
</script>

<style scoped lang="less">
	.player {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		z-index: 100;

		.box {
			width: 100%;
			height: 667px;
			// background-color: hotpink;
			position: relative;

			.bj {
				width: calc(100% / 1);
				height: calc(100% / 1);
				filter: blur(25px);
				// opacity: 0.7;
				transform: scale(1.5);

				.zhezhao {
					background: rgba(82, 74, 74, 0.6);
					position: absolute;
					left: 0;
					top: 0;
					right: 0;
					bottom: 0;
				}
			}

			// 			.beijingtu{
			// 				width: 100%;
			// 				height: 100%;
			// 				background-color: pink;
			// 			}
			.baitiao {
				width: 96px;
				height: 137px;
				position: absolute;
				left: 173px;
				top: 0;
				z-index: 10;

				.content {
					transform-origin: 0 0;
					transform: rotateZ(-25deg)
				}

				img {
					width: 100%;

				}
			}

			.middle {
				position: absolute;
				width: 100%;
				height: 296px;
				left: 0;
				top: 70px;

				.tp {
					width: 296px;
					height: 296px;
					margin: 0 auto;

					// z-index:9;
					img {
						width: 100%;
					}
				}

				.songpictures {
					position: absolute;
					width: 190px;
					height: 190px;
					left: 95px;
					top: 55px;

					.xuanzhuan {
						width: 184px;
						height: 184px;
						animation: que 20s linear infinite;

						img {
							width: 100%;
							height: 100%;
							border-radius: 50%;
							// animation-play-state: paused;
						}

						@keyframes que {
							0% {
								sform: rotate(0deg);
							}

							50% {
								transform: rotate(180deg);
							}

							100% {
								transform: rotate(360deg);
							}
						}
					}

					.qc {
						animation-play-state: paused;
					}
				}
			}

			.base {
				width: 100%;
				height: 80px;
				position: absolute;
				left: 0;
				bottom: 0;
				// border-top: 1px solid #cccccc;
				.s {
					color: #fff;
					// height: 30px;
					// line-height: 30px;
					.k {
						position: absolute;
						width: 16px;
						font-size: 12px;
						float: left;
						left: 35px;
						top: -7px;
					}
					// 进度条
					.progress {
						float: left;
						margin-left: 65px;
						// margin-top: 16px;
						width: 250px;
						height: 3px;
						position: relative;
						background: #fff;
						// 激活层
						.active-progress {
							width: 0;
							position: absolute;
							left: 0;
							top: 0;
							height: 3px;
							background-image: linear-gradient(to right, #C6E0F2, #6CA7F0);
						}

						// 绑定层
						.layer {
							position: absolute;
							left: 0;
							top: -4px;
							height: 8px;
							width: 100%;
							z-index: 5;
						}

						// 滑块
						.mask {
							position: absolute;
							left: 0;
							top: -3px;
							width: 9px;
							height: 9px;
							border-radius: 50%;
							background: rgb(236, 236, 102);
						}
					}

					.j {
						font-size: 12px;
						float: right;
						margin-right: 18px;
						margin-top: -7px;
					}
				}



				.shangyishou {
					width: 40px;
					height: 40px;
					float: left;
					margin-left: 100px;
					margin-top: 20px;

					img {
						width: 100%;
					}
				}

				.pofang {
					width: 40px;
					height: 40px;
					float: left;
					margin-left: 30px;
					margin-top: 20px;

					img {
						width: 100%;
					}
				}

				.xiayishou {
					width: 40px;
					height: 40px;
					float: left;
					margin-left: 30px;
					margin-top: 20px;

					img {
						width: 100%;
					}
				}
			}
		}
	}
</style>
